import React, { useEffect, useMemo, useState } from 'react';
import './UserGraph.scss';
import cx from "classnames";
import { ImSpinner2 } from 'react-icons/im';
import * as ChartModule from "chart.js/auto";
import { Line } from 'react-chartjs-2';
import zoom from 'chartjs-plugin-zoom';
import 'chartjs-adapter-date-fns';
import { daysToMs, findLatestOrEarliest } from '../../../../components/utils/utils';
import { eachDayOfInterval, format } from 'date-fns';
import { ALL_MARKETS } from '../../../../components/utils/constants';
import { CHART_COLORS } from '../../analyticsConstants';
import { floor } from '../../../../components/utils/math';
import { CHART_LABELS } from './../../analyticsConstants';
import Loader from '../../../../components/UI/loader/Loader';

const UserGraph = (props) => {
	const {
		txData,
		isLoading,
	} = props;
	const hasTxData = Boolean(Object.keys(txData).length && txData[Object.keys(txData)[0]]);

	const [profitPerps, profitOptions, profitTotal] = useMemo(() => {
		if (hasTxData) {
			console.log(txData);
			const profitPerps = txData.perps.reduce((profit, tx) => {
				return {
					sum: tx.cumulative_amount_usd,
					outflow: tx.daily_amount_usd >= 0 ? profit.outflow : profit.outflow + tx.daily_amount_usd
				}
			}, {
				sum: 0,
				outflow: 0
			});
			profitPerps.percentage = profitPerps.sum / profitPerps.outflow * 100;
			
			const profitOptions = txData.options.reduce((profit, tx) => {
				return {
					sum: tx.cumulative_amount_usd,
					outflow: tx.daily_amount_usd >= 0 ? profit.outflow : profit.outflow + tx.daily_amount_usd
				}
			}, {
				sum: 0,
				outflow: 0
			});
			profitOptions.percentage = profitOptions.sum / profitOptions.outflow * 100;
			
			const profitTotal = {
				sum: profitPerps.sum + profitOptions.sum,
				outflow: profitPerps.outflow + profitOptions.outflow,
			};
			profitTotal.percentage = profitTotal.sum / profitTotal.outflow * 100;
	
			return [profitPerps, profitOptions, profitTotal];
		}
		else return [];
	}, [txData]);
	
	const profitPerpsSign = profitPerps?.sum < 0 ? "" : "";
	const profitOptionsSign = profitOptions?.sum < 0 ? "" : "";
	const profitTotalSign = profitTotal?.sum < 0 ? "" : "";

	const [chartData, setChartData] = useState(null);
	useEffect(() => {
		if (!hasTxData) {
			setChartData(null);
		}
	}, [txData]);

	const Chart = ChartModule.default;
	Chart.register(zoom);
	useEffect(() => {
		if (hasTxData) {
			const datesArr = [
				...txData.perps.map(tx => tx.timestamp),
				...txData.options.map(tx => tx.timestamp),
			].sort();
			// Remove duplicate values
			const dates = [...new Set(datesArr)];
			const labels = dates.map(date => (new Date(date)).getTime());

			const datasets = [];
			// Only sum is used for chart
			// for (let i = 0; i < Object.keys(txData).length; i++) {
				// const txSource = Object.keys(txData)[i];
				const txSource = 'sum';
				const color = CHART_COLORS[txSource];
				const dataset = {
					label: CHART_LABELS[txSource],
					borderColor: color,
					backgroundColor: color,
					pointBackgroundColor: "transparent",
					hoverBackgroundColor: color,
					hoverBorderColor: color,
					tooltip: {
						callbacks: {
							labelColor: () => {
								return {
									backgroundColor: color,
									borderColor: color,
									borderWidth: 0,
								}
							},
						}
					},
					data: [],
				}
				
				let reducer = 0;
				for (let j = 0; j < dates.length; j++) {
					const date = dates[j];
					const dateTx = txData[txSource].find(tx => tx.timestamp === date);
					// Line Chart
					const profit = dateTx?.cumulative_amount_usd;
					// Bar Chart
					// const profit = dateTx?.daily_amount_usd;
					// Reducing number of dates to avoid multi-pointing
					if (reducer % 4 === 0) {
						dataset.data.push(dateTx ? floor(profit, 2) : undefined);
					} else {
						dataset.data.push(undefined)
					}
					if (dateTx) {
						reducer++;
					}
				}

				datasets.push(dataset);
			// }
			
			const chartData = {
				labels,
				datasets,
			}
			setChartData(chartData);
		}
	}, [txData]);

	const defaultFont = {
		family: "'Gantari', sans-serif",
		weight: 500,
	};
	const globalOptions = {
		spanGaps: true,
		pointStyle: 'circle',
		pointRadius: 6,
		pointHoverRadius: 4,
		pointHitRadius: 4,
		pointBackgroundColor: "transparent",
		pointBorderColor: "transparent",
		borderWidth: 2,
		interaction: {
			mode: 'x',
			intersect: true,
			mode: 'point',
		},
		maintainAspectRatio: false,
		layout: {
			padding: {
				top: 0,
				left: 0,
				right: 0,
				bottom: 10
			}
		}
	}
	const axesOptions = {
		grid: {
			display: false,
		},
	};
	const ticksOptions = {
		font: {
			...defaultFont,
			size: 14,
		},
		color: '#384263',
	}
	const legendOptions = {
		display: false,
		position: 'bottom',
		align: 'end',
		labels: {
			usePointStyle: true,
			pointStyleWidth: 9,
			boxHeight: 6,
			boxWidth: 6,
			color: '#fff',
			font: {
				...defaultFont,
				size: 15,
			},
			padding: 10
		},
	};
	const zoomOptions = {
		zoom: {
			enabled: false,
			mode: 'xy',
			// wheel: {
			// 	enabled: true
			// },
		},
		pan: {
			enabled: false,
			mode: 'y',
		},
		pinch: {
			enabled: false
		},
	};
	const tooltipOptions = {
		titleColor: '#3A4466',
		titleFont: {
			...defaultFont,
			size: 12,
		},
		bodyFont: {
			...defaultFont,
			size: 12,
			weight: 400
		},
		position: 'nearest',
		usePointStyle: true,
		pointBackgroundColor: "transparent",
		boxWidth: 6,
		borderWidth: 1,
		borderColor: 'rgba(56, 66, 99, 0.2)',
		backgroundColor: '#141637',
		cornerRadius: 5,
		padding: 8,
		caretPadding: 5,
		boxPadding: 4,
		xAlign: 'left',
		yAlign: 'top',
		callbacks: {
			label: function(context) {
				let label = context.dataset.label || '';
				if (label) {
						label += ': ';
				}
				if (context.parsed.y !== null) {
						label += new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(context.parsed.y);
				}
				return label;
			},
		},
	};
	const pluginsOptions = {
		legend: {
			...legendOptions,
		},
		zoom: {
			...zoomOptions,
		},
		tooltip: {
			...tooltipOptions,
		}
	};
	return (
		<div className="UserGraph">
			<h1>History</h1>
			<div className="UserGraph__content">
				<div className="UserGraph__graph App-box">
					<div className="UserGraph__graph-canvas">
						{isLoading
							? <Loader />
							: chartData && (
								<Line
									dataIdKey='txData'
									options={{
										...globalOptions,
										scales: {
											x: {
												...axesOptions,
												type: 'time',
												time: {
													minUnit: 'week',
													displayFormats: {
														day: 'd MMM'
													},
													tooltipFormat: 'd MMM yyyy',
												},
												ticks: {
													...ticksOptions,
												},
											},
											y: {
												...axesOptions,
												position: 'right',
												ticks: {
													...ticksOptions,
													callback: (value) => {
														let sign = '';
														if (value < 0) {
															sign = '-';
														}
														return `${sign}$${Math.abs(value).toFixed(0)}`;
													}
												},
											}
										},
										plugins: {
											...pluginsOptions,
										}
									}}
									data={chartData}
								/>
							)}
					</div>
					<div className="UserGraph__graph-markets-icons">
						{ALL_MARKETS.map(market => {
							const iconSrc = require(`../../../../img/icon-${market}_sm.svg`).default;
							return (
								<div className="UserGraph__graph-markets-icon" key={market}>
									<img src={iconSrc} alt={`${market} icon`} />
								</div>
							)
						})}
					</div>
				</div>
				<div className="UserGraph__overview App-box">
					<h2>Overview</h2>
					<div className="UserGraph__overview-items">
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Perps)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitPerps !== undefined && (
									<>
										{profitPerpsSign}${Math.abs(profitPerps.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitPerps?.sum > 0,
												negative: profitPerps?.sum < 0,
												muted: profitPerps?.sum === 0,
											})}>
												({profitPerpsSign || '+'}{(isNaN(profitPerps.percentage) ? 0 : Math.abs(profitPerps.percentage)).toFixed(0)}%)
										</span>
									</>
								)}
								
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Options)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitOptions !== undefined && (
									<>
										{profitOptionsSign}${Math.abs(profitOptions.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitOptions?.sum > 0,
												negative: profitOptions?.sum < 0,
												muted: profitOptions?.sum === 0,
											})}>
												({profitOptionsSign || '+'}{((isNaN(profitOptions.percentage) ? 0 : Math.abs(profitOptions.percentage))).toFixed(0)}%)
										</span>
									</>
								)}
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Total Realized PnL
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitTotal !== undefined && (
									<>
										{profitTotalSign}${Math.abs(profitTotal.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitTotal?.sum > 0,
												negative: profitTotal?.sum < 0,
												muted: profitTotal?.sum === 0,
											})}>
												({profitTotalSign || '+'}{(isNaN(profitTotal.percentage) ? 0 : Math.abs(profitTotal.percentage)).toFixed(0)}%)
										</span>
									</>
								)}
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default UserGraph;