import React, { useEffect, useState } from 'react';
import './UserProfile.scss';
import UserGraph from './components/UserGraph';
import TxHistory from './components/TxHistory';
import { FLIPSIDE_API_KEY, getSqlQueries, getSqlQueriesForChart } from '../analyticsConstants';
import { Flipside } from '@flipsidecrypto/sdk';
import StringInput from './../../../components/UI/input/StringInput';

const UserProfile = ({ walletAddress }) => {
	const [txData, setTxData] = useState([]);
	const [txDataForChart, setTxDataForChart] = useState({});
	const [account, setAccount] = useState("");
	const [isLoading, setIsLoading] = useState(true);

	const clearTxData = () => {
		setTxData([]);
		setTxDataForChart({});
	}
	
	// Setting title
	useEffect(() => {
		document.title = "User Profile | DeDeLend";
	}, []);

	// Fetching user tx data
	useEffect(() => {
		// Setting default account address
		if (walletAddress) {
			clearTxData();
			setAccount(walletAddress);
		}
	}, [walletAddress]);
	
	useEffect(() => {
		if (account) {
			// Running SQL-queries
			const keys = [];
			const flipside = new Flipside(
				FLIPSIDE_API_KEY,
				"https://api-v2.flipsidecrypto.xyz",
			);
			
			const queriesObj = getSqlQueries(account);
			const queriesObjForChart = getSqlQueriesForChart(account);
			const promises = [];
			const promisesChart = [];
			setIsLoading(true);
			for (const queryName of Object.keys(queriesObj)) {
				promises.push(
					flipside.query.run({ sql: queriesObj[queryName], ttlMinutes: 24 * 60 })
						.then(res => {
								res.records?.forEach(record => {
									if (keys.find(key => key === record.tx_hash)) {
										return;
									}
									keys.push(record.tx_hash);
									record.market = queryName;
									txData.push(record);
								})
								setTxData(txData.slice());
							}, console.log)
						);
			}
			Promise.all(promises)
				.then(() => {
					setTxData(txData.slice());
				})
			
			for (const queryObj of queriesObjForChart) {
				promisesChart.push(
					flipside.query.run({ sql: queryObj.query, ttlMinutes: 24 * 60 })
						.then(res => {
							txDataForChart[queryObj.name] = res.records;
						}, console.log)
				)
			}
			Promise.all(promisesChart)
				.then(() => {
					setTxDataForChart({ ...txDataForChart });
				})

			// Hide loader after all promises resolved
			Promise.all([...promises, ...promisesChart])
				.then(() => {
					setIsLoading(false);
				})
		}
	}, [account]);

	return (
		<div className="UserProfile">
			<div className="UserProfile__account">
				<div className="UserProfile__account-input">
					{/* <StringInput
						value={account}
					/> */}
				</div>
			</div>
			<UserGraph
				txData={txDataForChart}
				isLoading={isLoading}
			/>
			<TxHistory
				txData={txData}
				isLoading={isLoading}
			/>
		</div>
	);
};

export default UserProfile;